.App {
  background-color: #eee;
  padding-top: 1px;
  margin: 0px;
  color: #000;
}

.main-container {
  border-radius: 2px;
  padding: 5px;
  margin: 15px 5px;
  background-color: #fff;
}

.main-flex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.navbar {
  background-color: #eb4729;
  padding-top: 1px;
  padding-bottom: 20px;
  height: 60px;
  width: 100vw;
}

h1 {
  color: #FFF;
}

h2 {
  color: #333;
  font-weight: normal;
  text-transform: uppercase;
}
